@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
:root{

    /* COLORS */
    --gray: rgb(150, 167, 175);
    --green: rgb(3, 140, 101);
    --green-dark: rgb(26, 40, 47);
    --green-soft-dark: rgb(34, 52, 60);
    --red: rgb(255, 87, 95);
    --white:hsl(0, 0%, 100%);
    --yellow: rgb(255, 197, 66);

    /* COLORS VARIABLES */

    --color-primary: var(--green);
    --color-primary-dark: var(--green-dark);
    --color-primary-soft-dark: var(--green-soft-dark);
    --color-gray: var(--gray);

    --color-bg-main: var(--color-primary-dark);
    --color-bg-primary: var(--color-primary);
    --color-bg-components: var(--color-primary-soft-dark);

    --color-font-primary: var(--white);
    --color-font-secundary: var(--color-gray);

    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
}
html,
body {
